body {
  margin: 0;
  padding: 0;
  font-family: "Work Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  color: rgba(0, 0, 0, 0.55);
  line-height: 2;
}

.color-primary {
  color: #09c0f7 !important;
}

.color-grey-54 {
  color: rgba(0, 0, 0, 0.55) !important;
}

.color-grey-38 {
  color: rgba(0, 0, 0, 0.38) !important;
}

.text-medium {
  font-weight: 500;
}

.text-size-14 {
  font-size: 14px;
}

.title-deco {
  display: -webkit-flex;
  display: flex;
  text-transform: uppercase;
  font-weight: bolder;
  -webkit-align-items: center;
          align-items: center;
  color: #09c0f7;
}

.title-deco:before {
  display: -webkit-inline-flex;
  display: inline-flex;
  content: "";
  width: 50px;
  height: 2px;
  background-color: #09c0f7;
  margin-right: 8px;
}

.section-subtitle {
  font-weight: bolder;
  color: rgba(0, 0, 0, 0.24);
  text-transform: uppercase;
}

.gradient-text {
  color: #09c0f7;
  background-image: linear-gradient(-180deg, #09c0f7 0%, #09c0f7 100%);
  -webkit-text-fill-color: rgba(0, 0, 0, 0);
}

@media (max-width: 639px) {
  main .resume {
    padding: 0 32px;
  }

  main .contacts {
    padding: 0 32px;
  }
}

/* ---------- Main Div ---------- */
.container {
  text-align: center;
}
.photo-wrapper {
  padding: 6vmin;
}
.photo {
  border-radius: 50%;
  box-shadow: 0 0 1px 11px rgba(22, 136, 254, 0.1),
    0 0 1px 22px rgba(22, 136, 254, 0.05);
  border: 4px solid rgba(22, 136, 254, 0.5);
  width: 20vmin;
}
.title {
  margin-top: 1vh;
  color: #09c0f7;
}
.profile-section {
  display: -webkit-flex;
  display: flex;
}

.resume .title-deco {
  margin-bottom: 24px;
}
.paragraph {
  font-size: 2.3vmin;
}

.circle-text-container {
  width: 80px;
  height: 80px;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border: 2px solid rgba(0, 0, 0, 0.39);
  border-radius: 50%;
  padding: 8px;
  margin-top: 16px;
  margin-right: 16px;
  text-align: center;
}

.core-skills-list .core-skill:nth-child(odd) {
  border-color: #09c0f7;
}

.other-skills-list .other-skill {
  border-radius: 2px;
  line-height: 44px;
  background-color: #f5f5f5;
  margin-top: 16px;
  margin-right: 16px;
  padding-left: 10px;
  padding-right: 10px;
  text-transform: uppercase;
  font-size: 14px;
}

.edu-item:not(:first-child) {
  margin-top: 16px;
}

.edu-item .year > span {
  background-color: #7ae4eb;
  color: white;
  padding: 0 5px;
  border-radius: 2px;
}

.edu-item .year:after {
  content: "";
  width: 6px;
  display: -webkit-inline-flex;
  display: inline-flex;
  height: 9px;
  background-color: #09c0f7;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  margin-left: -4px;
}

.hobby-list .hobby-item {
  width: 64px;
  height: 64px;
  border-color: #09c0f7;
}

@media screen and (min-width: 360px) and (max-width: 640px) {
  .paragraph {
    font-size: 3vmin;
  }
}

.contacts .title-deco {
  margin-bottom: 24px;
}

.mail-phone {
  font-size: 20px;
}

.social-item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.social-item a {
  color: rgba(0, 0, 0, 0.55);
  transition: all 0.3s;
}
.social-item a:hover {
  color: rgba(0, 0, 0, 0.87);
}

.social-item .icon {
  height: 40px;
  width: 40px;
  background-color: #09c0f7;
  color: white;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 100%;
  padding: 4px;
}

.social-list .row-2 {
  margin-top: 16px !important;
}

.contact-form .uk-input,
.contact-form .uk-select,
.contact-form .uk-textarea {
  background-color: whitesmoke;
  padding: 0 16px;
}

.contact-form .uk-input {
  height: 48px;
}

.contact-form .uk-textarea,
.contact-form .uk-input {
  border-color: whitesmoke;
  border-radius: 5px;
}

.contact-form .uk-input:focus,
.contact-form .uk-textarea:focus {
  border: 1px solid whitesmoke;
  background-color: white !important;
}

.contact-form .submit-btn {
  background-color: #09c0f7;
  color: white;
  border-radius: 4px;
}

@media (max-width: 450px) {
  .row-1,
  .row-2 {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .row-1 .social-item:last-child,
  .row-2 .social-item:last-child {
    margin-top: 16px;
  }

  .mail-phone > div > span {
    display: block;
  }
}

.project-card .uk-card-title {
  font-size: 1.25rem;
}

.project-card .tags {
  text-transform: uppercase;
  font-size: 14px;
}

.project-card .tags span:not(:last-child):after {
  content: " / ";
}

.project-card .uk-card {
  box-shadow: none;
}

.project-card .uk-button-line {
  background-color: transparent;
  font-size: 16px;
  border-bottom: 2px solid #09c0f7;
  padding: 0;
  transition: all 0.3s;
}

.project-card .uk-button-line:hover {
  color: #09c0f7;
}

.project-card a:hover {
  color: black;
}

.project-card .uk-card > a {
  display: block;
  position: relative;
}

.project-card .uk-card .img-overlay {
  top: 0;
  width: 100%;
  height: 100%;

  position: absolute;
  transition: all 0.3s;
}
.project-card .uk-card .img-overlay:hover {
  background-color: rgba(0, 0, 0, 0.24);
}

.projects .more-project {
  background-color: #09c0f7;
  border-radius: 2px;
  color: white;
  border: 2px solid #09c0f7;
  transition: all 0.3s;
}

.projects .more-project:hover {
  background-color: white;
  color: #09c0f7;
}

.projects {
  margin-top: 10vmin;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

footer .uk-navbar-nav li > a {
  width: 50px;
  height: 50px;
  background-color: #09c0f7;
  border-radius: 50%;
  color: whitesmoke;
  border: 1px solid #09c0f7;
  transition: all 0.3s;
  padding: 0 15px !important;
  min-height: 55px !important;
}

footer .uk-navbar-nav li:hover > a,
footer .uk-navbar-nav > li > a:focus {
  background-color: white;
  color: #09c0f7;
  border-color: #09c0f7;
  cursor: pointer !important;
}

footer .uk-navbar-nav li:not(:last-child) {
  margin-right: 16px;
}

header {
  background-color: white;
}

.uk-navbar-nav > li > a.active,
.uk-navbar-nav > li:hover > a.active {
  color: #09c0f7 !important;
  cursor: pointer !important;
}

