.project-card .uk-card-title {
  font-size: 1.25rem;
}

.project-card .tags {
  text-transform: uppercase;
  font-size: 14px;
}

.project-card .tags span:not(:last-child):after {
  content: " / ";
}

.project-card .uk-card {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.project-card .uk-button-line {
  background-color: transparent;
  font-size: 16px;
  border-bottom: 2px solid #09c0f7;
  padding: 0;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.project-card .uk-button-line:hover {
  color: #09c0f7;
}

.project-card a:hover {
  color: black;
}

.project-card .uk-card > a {
  display: block;
  position: relative;
}

.project-card .uk-card .img-overlay {
  top: 0;
  width: 100%;
  height: 100%;

  position: absolute;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.project-card .uk-card .img-overlay:hover {
  background-color: rgba(0, 0, 0, 0.24);
}

.projects .more-project {
  background-color: #09c0f7;
  border-radius: 2px;
  color: white;
  border: 2px solid #09c0f7;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.projects .more-project:hover {
  background-color: white;
  color: #09c0f7;
}

.projects {
  margin-top: 10vmin;
}
